td {
  border: 2px solid;
width: 40px;
height: 40px;
}

.board-item{
    font-size: 35px;
    width: 40px;
    height: 40px;

}
.winner{
  color: #567458;
    font-size: 40px;
    font-family: monospace;
}
.App {
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

@media screen and (max-width: 480px) {
  .winner{
      font-size: 20px;
    }
    .board-item{
        font-size: 18px;
        width: 20px;
        height: 20px;
    }
    td {
    width: 20px;
    height:20px;
    }
}
.valo{
  background-color: #003333;
}
.valx{
  background-color: #663333;
}

.reset {
  margin-top: 50px;
  width: 100px;
  margin-left: 178px;
  cursor: pointer;
  background: #ffdc7d;
  background-image: -webkit-linear-gradient(top, #ffdc7d, #7388ff);
  background-image: -o-linear-gradient(top, #ffdc7d, #7388ff);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#ffdc7d), to(#7388ff));
  background-image: linear-gradient(to bottom, #ffdc7d, #7388ff);
  border-radius: 18px;
  -webkit-box-shadow: 2px 2px 24px #0a732d;
  box-shadow: 2px 2px 24px #0a732d;
  font-family: Georgia;
  color: #ffffff;
  font-size: 37px;
  padding: 10px 20px 10px 20px;
  text-decoration: none;
}

.reset:hover {
  background: #ffb700;
  background-image: -webkit-linear-gradient(top, #ffb700, #0026ff);
  background-image: -o-linear-gradient(top, #ffb700, #0026ff);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#ffb700), to(#0026ff));
  background-image: linear-gradient(to bottom, #ffb700, #0026ff);
  text-decoration: none;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



